import Vue from 'vue'
import App from './App.vue'
import router from './router';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import './assets/styles/common.css';  // 引入公共样式
import VueMeta from 'vue-meta'
import ErrorLoggerPlugin from './plugins/errorLogger'
//test
Vue.config.productionTip = false
Vue.use(ElementUI);
Vue.use(VueMeta,{
  keyName: 'metaInfo',
  tagIDKeyName: 'vmid',
  refreshOnceOnNavigation: true,
  routeMetaKey: false    // ← 加上这行，永别所有问题！
})
 Vue.use(ErrorLoggerPlugin)
// 页面切换时动态设置 <title>
router.afterEach((to) => {
  if (to.name) {
    document.title = to.name;
  }
  if (process.env.NODE_ENV !== 'development' && window.__PRERENDER_INJECTED__ !== true)
  {
    if (typeof window.gtag === 'function' ) {
      setTimeout(() => {
        window.gtag('event', 'page_view', {
          page_path: to.fullPath,
          page_title: to.meta.title || document.title,
          send_to: 'G-MTDVMYZDBN' // 您的衡量 ID
        });
        // console.log(`[GA] 发送页面浏览事件: ${to.fullPath}`); // 调试用
      }, 0); // 延迟 0ms，实现异步非阻塞
    }



  } else {
    // console.log(`[GA] 开发环境，跳过发送页面浏览事件: ${to.fullPath}`); // 调试用
  }
});

new Vue({
  router,
  render: h => h(App),
  mounted () {
    // 让 prerender-spa-plugin 知道页面渲染完了
    document.dispatchEvent(new Event('render-event'))

  }
}).$mount('#app')
