
// 核心配置：允许访问的域名和IP模式列表 (使用 allowedHosts 更规范)
const allowedHosts = [
    '4yop.github.io',
    '*.4yop.com',
    '127.0.0.1',
    'localhost',
    '192.168.*.*'
];

class RiskUtil
{
    /**
     * 检查当前域名是否在允许列表中（支持通配符 *）
     * @param {string} hostname - 当前页面的域名 (currentHostname)
     * @returns {boolean} - 如果匹配成功，返回 true
     */
    static isHostAllowed(hostname)
    {
        const host = hostname.toLowerCase().split(':')[0]; // 去除可能的端口
        for (let pattern of allowedHosts)
        {
            pattern = pattern.toLowerCase();

            // 精确匹配
            if (pattern === host) return true;

            // 处理含通配符的模式
            if (!pattern.includes('*'))
            {
                continue;
            }


            // 特殊处理 IP 段通配符
            if (pattern.startsWith('192.168.'))
            {
                const regex = /^192\.168\.(25[0-5]|2[0-4]\d|1\d{2}|[1-9]?\d)\.(25[0-5]|2[0-4]\d|1\d{2}|[1-9]?\d)$/;
                if (regex.test(host)) return true;
                continue;
            }

            // 处理域名通配符：只允许 *.example.com 形式，且 * 只代表一级标签
            // 正确做法：将 *.example.com 转为 ^([^.]+)\.example\.com$
            const parts = pattern.split('*.');
            if (parts.length === 2 && parts[0] === '')
            {
                const suffix = parts[1].replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
                const regex = new RegExp(`^[^.]+\\.${suffix}$`);
                if (regex.test(host)) return true;
            }

            // 若需支持更复杂通配（如 *.*.4yop.com），可另行扩展

        }
        return false;
    }

    static jump(path)
    {
        const currentHostname = window.location.hostname;
        const primaryHost = '4yop.github.io'; // 主要重定向目标
        //console.log(to.path)

        // 执行风控检查 process.env.NODE_ENV !== 'development' &&
        if (window.__PRERENDER_INJECTED__  !== true && process.env.NODE_ENV !== 'development' && !RiskUtil.isHostAllowed(currentHostname))
        {
            // // 构造重定向目标 URL
            const redirectTargetUrl = `https://${primaryHost}${path}`;
            //
            // // 执行跳转，防止页面内容被盗用
            window.location.replace(redirectTargetUrl);
            window.location.href = redirectTargetUrl;
        }
    }


}







// 如果检查通过 (isHostAllowed 返回 true)，代码自然结束，页面继续加载。
export default RiskUtil