/**
 * 设备检测工具类
 */
export class DeviceUtil {
    /**
     * 检测是否为移动设备
     * @param {number} breakpoint - 移动设备断点，默认768px
     * @returns {boolean} 是否为移动设备
     */
    static isMobile(breakpoint = 768) {
        return window.innerWidth <= breakpoint;
    }

    /**
     * 检测是否为平板设备
     * @returns {boolean} 是否为平板设备
     */
    static isTablet() {
        return window.innerWidth > 768 && window.innerWidth <= 1024;
    }

    /**
     * 检测是否为桌面设备
     * @returns {boolean} 是否为桌面设备
     */
    static isDesktop() {
        return window.innerWidth > 1024;
    }

    /**
     * 获取设备类型
     * @returns {string} 设备类型 ('mobile', 'tablet', 'desktop')
     */
    static getDeviceType() {
        if (this.isMobile()) return 'mobile';
        if (this.isTablet()) return 'tablet';
        return 'desktop';
    }

    /**
     * 添加窗口大小变化监听器
     * @param {Function} callback - 回调函数
     * @returns {Function} 移除监听器的函数
     */
    static addResizeListener(callback) {
        window.addEventListener('resize', callback);
        return () => window.removeEventListener('resize', callback);
    }

    /**
     * 检测是否为移动设备（兼容App.vue中的600px断点）
     * @returns {boolean} 是否为移动设备
     */
    static isMobileCompat() {
        return window.innerWidth <= 600;
    }
}

export default DeviceUtil
